@keyframes bounce{0%,80%,100%{transform:scale(0.1)}40%{transform:scale(1)}}.wrapper{display:inline-block}.loader{margin:0;width:32px;text-align:center;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.loader.big{width:64px}.ball{width:8px;height:8px;background-color:#0058a5;border-radius:100%;display:block;animation:bounce 1.4s ease-in-out 0s infinite both}.ball.big{width:16px;height:16px}.first{animation-delay:-0.32s}.second{animation-delay:-0.16s}.third{animation-delay:0s}
