.button{width:120px;height:30px;border-radius:3.5px;box-shadow:0 0.5px 0.5px 0 rgba(0,0,0,0.15);border:solid 0.5px #4ebfd7;background-color:#4ebfd7}.button:active{opacity:0.7}.light-green{background-color:#8cc7d4}.disabled{border-image-source:linear-gradient(to bottom, #d4d9de, #7f8083);border-image-slice:1;background-image:linear-gradient(to bottom, #dce2e9, #c2c5c7),linear-gradient(to bottom, #d4d9de, #7f8083);background-origin:border-box}.disabled:active{opacity:1}.label{font-size:13px;color:#ffffff}.loader-btn{width:100%;height:100%;display:flex;justify-content:center;align-items:center}
